.DonatePage {
    width: 100%;
    height: 100%;
    min-height: calc(100vh - 110px) !important;
}

.DonatePage .downloadInProgress {
    margin: 50px 0;
}

.DonatePage .downloadInProgress .Label {
    width: 100%;
    font-size: 20pt;
    text-align: center;
}

.DonatePage .helpButton {
    position: fixed;
    bottom: 30px;
    right: 30px;
    margin: 30px auto;
    padding: 15px 35px;
    color: black;
    background-color: #ff6720;
    text-decoration: none;
}

.DonatePage .helpButton:hover {
    background-color: #ffb120;
}

.DonatePage .donateInfo {
    margin: 20px;
    font-size: 13pt;
}

.DonatePage .ButtonArea {
    display: block;
}

.DonatePage .ButtonArea .LinkButton {
    margin: 10px;
    padding: 25px;
    width: auto;
    font-size: 20pt;
}

.DonatePage .ButtonArea .LinkButton:hover {
    background-color: #ffb120;
}

.DonatePage .thankyou {
    margin: 10px auto 30px auto;
    font-size: 13pt;
    color: gray;
}

.githubButton {
    display: flex;
    margin: auto;
    padding: 15px 35px;
    width: 100%;
    font-size: 30px;
    color: white;
    background: #000000;
    align-items: center;
    gap: 10px;
}

.githubButton:hover {
    background: #575757;
}

.githubButton:active {
    background: #a0a0a0;
}